.forecast-container {
    margin-left: 2%;
    width: 93%;
    position: absolute;
    opacity: 0;
    transition: 0.3s position ease-in-out;
    transition: 0.3s opacity ease-in-out;
    margin-top:5%;
    top:500%;
}

.forecast-container.visible {
    position: static;
    opacity: 1;
}

.daily-item {
    background-color: #212121;
    border-radius: 6px;
    height: 2.7vh;
    margin: 1vh;
    display: flex;
    align-items: center;
    cursor: pointer;
    font-size: 1vw;
    padding: 0.8vw 1vw;
    box-shadow: 2px 2px 5px #33333367;
    opacity: 0.75;
    transform: scale(0.99);
    transition: 0.2s all ease-in-out;

}



.icon-small {
    width: 2.7vh;
    height: auto;
}

.day {
    color: #757575;
    flex: 1 1;
    font-weight: 600;
    margin-left: 1.4vh;
}

.description {
    flex: 1 1;
    margin-right: 1.4vh;
    text-align: right;
    color: #757575;
    
}


.daily-details-grid {
    grid-row-gap: 0;
    grid-column-gap: 1.4vh;
    row-gap: 0px;
    column-gap: 1.4vh;
    display: grid;
    flex: 1 1;
    grid-template-columns: auto auto;
    padding: 5px 15px;
    font-size: 2vh;


}

.daily-details-grid-item {
    align-items: center;
    display: flex;
    height: 2.4vh;
    justify-content: space-evenly;
    width: 95%;
}

.daily-details-grid-desc {
    color: #ffcd70b8;
    text-shadow: 2px 2px 10px #FFCC70;
}

.daily-details-grid-value {
    color: #ffcd70b5;
    text-shadow: 2px 2px 10px #FFCC70;
}

.min-max {
    color: #757575;;
}

.daily-item:hover {
    opacity: 1;
    transform: scale(1);
    background: #0F2027;
    /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #2C5364, #203A43, #0F2027);
    /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #2C5364, #203A43, #0F2027);
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */


}

.daily-item:hover .description,
.daily-item:hover .min-max {
    color: #f9f9f9;
    text-shadow: 2px 2px 10px #f9f9f9;
}

.daily-item:hover .day {
    color: #FFCC70;
    text-shadow:2px 2px 10px #FFCC70;
}

/* LIGHT */ 
.daily-item.light:hover {
    opacity: 1;
    transform: scale(1);
    background: #2193b0;
    /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, rgba(110, 214, 237,0.5), rgba(33, 147, 176,0.5));
    /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, rgba(110, 214, 237,0.5), rgba(33, 147, 176,0.8));
}
.daily-item.light:hover .day{ 
    color: #E1795F;
    text-shadow:2px 2px 10px #E1795F;
}
.daily-item.light:hover .description,
.daily-item.light:hover .min-max {
    color: #afed82;
    text-shadow: 2px 2px 10px #afed82;
}
.daily-details-grid.light .daily-details-grid-item .daily-details-grid-desc{
    color: #f9f871;
    text-shadow: 2px 2px 10px #f9f871;
}

.daily-details-grid.light .daily-details-grid-item .daily-details-grid-value{
    color: #f9f871;
    text-shadow: 2px 2px 10px #f9f871;
}